.contactus--section {
  margin-top: 4.6em;
  background-color: var(--home-logged-bg);
  width: 100%;
  padding: 3em 2em;
  font-family: "Poppins", sans-serif;
}
.contactus__heading__container {
  text-align: center;
  margin-bottom: 2.5em;
}
.contactus__heading__container h1 {
  font-family: "Nunito", cursive;
  margin-bottom: 0.25em;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  font-size: 2.3em;
  line-height: 1.15;
  color: var(--h1-color);
}
.contactus__heading__container p {
  color: var(--nav-heading-color);
  font-size: 1em;
}
.contactus__cards__container {
  --just: center;
  --dir: column;
  --align: center;
  display: flex;
  justify-content: var(--just);
  align-items: var(--align);
  flex-direction: var(--dir);
}

.card {
  width: 100%;
  margin: 0 auto;
  background-color: var(--color-white);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  padding: 2.5em 1em;
  border-radius: 0.3em;
  position: relative;
  transition: box-shadow 200ms ease-out;
}
.card:hover {
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.3);
}
.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 6px;
  background-color: var(--general-color);
  border-top-left-radius: 0.35em;
  border-top-right-radius: 0.35em;
}
.card__header h3 {
  margin-bottom: 1em;
  font-weight: 700;
  font-family: "Nunito", sans-serif;
  letter-spacing: 0.04em;
  color: var(--h3-color);
  position: relative;
}
.card__header h3:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3.2px;
  background-color: currentColor;
  border-radius: 1em;
}
.card__body p {
  color: #222;
}
.card__body p a {
  text-decoration: none;
  color: #5b24ca;
}
.card__body p a:hover, .card__body p a:focus {
  text-decoration: 2px underline currentColor;
  text-underline-offset: 3px;
}

.call__card {
  margin-bottom: 2em;
}
.call__card:before {
  background-color: #e4880f;
}

/***************************
MEDIA QUERIES
****************************/
@media (min-width: 400px) {
  .card {
    width: 80%;
  }
}
@media (min-width: 650px) {
  .contactus__cards__container {
    --just: center;
    --dir: row;
    --align: flex-start;
  }

  .card {
    width: 100%;
  }

  .call__card {
    margin-right: 1em;
  }

  .mail__card {
    margin-left: 1em;
  }
}
@media (min-width: 769px) {
  .contactus__heading__container {
    width: 70%;
    margin: 0 auto 3em;
  }

  .card {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .card {
    width: 35%;
    margin: 0 0 auto 0;
  }

  .call__card {
    margin-right: 1em;
  }

  .mail__card {
    margin-left: 1em;
  }
}